@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@200;400;700;900&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  background-color: black;
  overflow-x: hidden;
}

p {
  font-size: 2rem;
  color: black;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

