.header {
  background-image: url(../../../build/img/fondo.webp);
  background-repeat: no-repeat;
  background-size: cover;
  height: 95vh;
  width: 100%;
  font-family: "Roboto Slab", serif;
}

.navegacion {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
  transition: background-color 0.3s ease-in-out, height 0.2s ease-in-out;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  place-items: center;
  height: 10rem;
}
@media (min-width: 768px) {
  .navegacion {
    justify-content: space-between;
    display: flex;
    align-items: center;
  }
}
.navegacion .logo {
  filter: brightness(200%);
}
.navegacion .logo img {
  width: 30rem;
  filter: invert(100%);
}
.navegacion .logo-container {
  grid-row: 1/2;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.navegacion .logo-container .logo {
  padding-right: 6rem;
}
@media (min-width: 768px) {
  .navegacion .logo-container .logo {
    padding-right: 0rem;
  }
}
@media (min-width: 768px) {
  .navegacion .logo-container {
    flex-direction: column;
  }
}
.navegacion .enlaces,
.navegacion .adicionales {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  visibility: hidden;
  height: 0rem;
  width: 0rem;
  padding: 0rem;
}
@media (min-width: 768px) {
  .navegacion .enlaces,
  .navegacion .adicionales {
    flex-direction: row;
    padding: 1rem;
    width: auto;
    height: auto;
    visibility: visible;
    gap: 0.5rem;
  }
}
.navegacion .enlaces a,
.navegacion .adicionales a {
  transition: none;
  visibility: hidden;
  height: 0rem;
  width: 0rem;
  padding: 0rem;
  color: #FFFFFF;
  border: 1px solid rgba(255, 255, 255, 0);
  border-radius: 0.5rem;
  transition: 0.3s all;
}
@media (min-width: 768px) {
  .navegacion .enlaces a,
  .navegacion .adicionales a {
    visibility: visible;
    width: auto;
    height: auto;
    padding: 1rem;
  }
}
.navegacion .enlaces a:hover,
.navegacion .adicionales a:hover {
  border: 1px solid #FFFFFF;
  background-color: rgba(255, 255, 255, 0.301);
  color: #FFFFFF;
}

.fijamiento {
  height: 8rem;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
  background-color: rgb(10, 10, 10);
  transition: background-color 0.3s ease-in-out, height 0.2s ease-in-out;
  box-shadow: 0px 2px 3px rgba(255, 255, 255, 0.021);
}
@media (min-width: 768px) {
  .fijamiento {
    height: auto;
  }
}

.descripcion {
  display: flex;
  justify-content: space-between;
  margin: 5rem 1rem;
}
.descripcion.menosMargin {
  margin: 0rem 1rem;
}
@media (min-width: 768px) {
  .descripcion {
    margin: 30rem 2rem;
  }
}
.descripcion .location {
  display: flex;
  justify-content: center;
}
.descripcion .location img {
  width: 2.9rem;
}

.redes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
}
.redes .red img {
  transition: all 0.2s;
  width: 3.5rem;
}
.redes .red img:hover {
  cursor: pointer;
}
.redes .contact {
  display: flex;
  justify-content: center;
}
.redes .contact img {
  width: 3rem;
}

#twitter:hover {
  filter: drop-shadow(0px 0px 8px rgba(0, 183, 255, 0.662745098));
}

#whatsapp:hover {
  filter: drop-shadow(0px 0px 8px rgba(27, 215, 65, 0.662745098));
}

#facebook:hover {
  filter: drop-shadow(0px 0px 8px rgba(72, 103, 170, 0.662745098));
}

#youtube:hover {
  filter: drop-shadow(0px 0px 8px rgba(254, 0, 0, 0.662745098));
}

.boton-menu {
  visibility: visible;
  padding-right: 2rem;
  padding-left: 1rem;
}
.boton-menu:hover {
  cursor: pointer;
}
.boton-menu img {
  width: 4rem;
}
@media (min-width: 768px) {
  .boton-menu {
    visibility: hidden;
  }
  .boton-menu img {
    width: 0rem;
    height: 0rem;
  }
}

.navegacion .enlaces a.menu,
.navegacion .enlaces.menu,
.navegacion .adicionales a.menu,
.navegacion .adicionales.menu {
  font-family: "Roboto Slab", serif;
  font-weight: 900;
  padding: 1.5rem;
  width: 100%;
  height: auto;
  visibility: visible;
  gap: 1rem;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion .enlaces a.menu,
  .navegacion .enlaces.menu,
  .navegacion .adicionales a.menu,
  .navegacion .adicionales.menu {
    visibility: visible;
    width: auto;
    height: auto;
    padding: 1rem;
  }
}

.navegacion .adicionales.menu {
  margin-top: -2rem;
}

.moreHeight {
  height: 40rem;
}
@media (min-width: 768px) {
  .moreHeight {
    height: 10rem;
  }
}

.navegacion-productos {
  margin-bottom: 5rem;
}

.datos {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0;
  padding: 1rem;
  margin-top: 12rem;
}
@media (min-width: 1024px) {
  .datos {
    display: flex;
    flex-direction: row;
    gap: 10rem;
  }
}
.datos img {
  width: 7rem;
}
.datos .dato {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.datos .dato h2, .datos .dato p {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
}
.datos .dato h2 {
  font-size: 1.7rem;
  margin: 2rem 0 0 0;
  text-shadow: 1px 1px 1px #FFFFFF;
}
.datos .dato p {
  font-size: 1.5rem;
}

.slider {
  width: 100%;
  height: auto;
  margin: 15rem auto;
  overflow: hidden;
  background-color: #FFFFFF;
}

.slider .slide-track {
  display: flex;
  align-items: center;
  gap: 2rem;
  animation: scroll 40s linear infinite;
  -webkit-animation: scroll 40s linear infinite;
  width: 360rem;
}

.slider .slide {
  width: 20rem;
}

.slider .slide img {
  width: 100%;
}

@keyframes scroll {
  0% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-160rem);
    transform: translateX(-160rem);
  }
}
.productos {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 10rem;
  width: 100%;
  margin: 15rem 0 3rem 0;
}
.productos .portada {
  height: 50vh;
  width: 100%;
  background-image: url(../../../build/img/portada.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
  font-weight: 900;
  font-size: 2.5rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .productos .portada {
    font-size: 3.5rem;
  }
}

.buzosh {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .buzosh {
    flex-direction: row;
  }
}
.buzosh .buzos {
  display: grid;
  row-gap: 5rem;
  column-gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .buzosh .buzos {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .buzosh .buzos {
    row-gap: 10rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.buzosh .buzos .buzo {
  position: relative;
  width: 35rem;
}
.buzosh .buzos img {
  width: 100%;
}

.buzosh-portada {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../../build/img/buzohombre.webp);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35rem;
  height: 80rem;
}
@media (min-width: 768px) {
  .buzosh-portada {
    width: 72rem;
  }
}
@media (min-width: 1024px) {
  .buzosh-portada {
    width: 60rem;
  }
}
.buzosh-portada h1 {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
  text-align: center;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .buzosh-portada h1 {
    font-size: 10rem;
  }
}

.buzosm {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .buzosm {
    flex-direction: row;
  }
}
.buzosm .buzos {
  display: grid;
  row-gap: 10rem;
  column-gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .buzosm .buzos {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .buzosm .buzos {
    row-gap: 10rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.buzosm .buzos .buzo {
  position: relative;
  width: 35rem;
}
.buzosm .buzos img {
  width: 100%;
}

.buzosm-portada {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../../build/img/buzomujer.webp);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35rem;
  height: 85rem;
}
@media (min-width: 768px) {
  .buzosm-portada {
    width: 72rem;
  }
}
@media (min-width: 1024px) {
  .buzosm-portada {
    width: 60rem;
  }
}
.buzosm-portada h1 {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
  text-align: center;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .buzosm-portada h1 {
    font-size: 10rem;
  }
}

.remeras-portada {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url(../../../build/img/remerasu.webp);
  justify-content: center;
  display: flex;
  align-items: center;
  width: 35rem;
  height: 80rem;
}
@media (min-width: 768px) {
  .remeras-portada {
    height: 80.2rem;
    width: 72rem;
  }
}
@media (min-width: 1024px) {
  .remeras-portada {
    width: 60rem;
  }
}
.remeras-portada h1 {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
  text-align: center;
  font-size: 5rem;
}
@media (min-width: 768px) {
  .remeras-portada h1 {
    font-size: 10rem;
  }
}

.remerasu {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .remerasu {
    flex-direction: row;
  }
}

.remeras {
  display: grid;
  row-gap: 10rem;
  column-gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .remeras {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .remeras {
    row-gap: 10rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.remeras .remera {
  position: relative;
  width: 35rem;
}
.remeras .remera img {
  width: 100%;
}

.remera img:hover,
.buzo img:hover {
  filter: opacity(80%);
  transition: all 0.5s;
  overflow: hidden;
  cursor: pointer;
}

.ver-text {
  transition: opacity 0.5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 6rem;
  font-family: "Roboto Slab", serif;
  font-weight: bolder;
  color: #FFFFFF;
  text-shadow: -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black, 1px 1px 0 black;
  z-index: 1;
  opacity: 0;
  pointer-events: none;
}

.remera:hover .ver-text,
.buzo:hover .ver-text {
  opacity: 1;
}

.nosotros {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .nosotros {
    margin-top: 20rem;
  }
}
@media (min-width: 1024px) {
  .nosotros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
  }
}
.nosotros .nosotros-texto {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
}
.nosotros .nosotros-texto h1, .nosotros .nosotros-texto p {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
}
.nosotros .nosotros-texto h1 {
  font-size: 3rem;
  font-weight: bolder;
  margin: 1rem 0;
  border-bottom: 2px solid #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .nosotros .nosotros-texto h1 {
    font-size: 4rem;
  }
}
.nosotros .nosotros-texto p {
  font-weight: bold;
}
.nosotros .nosotros-texto iframe {
  width: 100%;
}

.footer {
  box-shadow: 0px 0px 1px #FFFFFF;
  background-color: rgba(10, 10, 10, 0.815);
  margin-top: 20rem;
  gap: 10rem;
  padding: 5rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .footer {
    padding-left: 8rem;
    flex-direction: row;
  }
}
.footer h1 {
  font-family: "Roboto Slab", serif;
  font-size: 2.5rem;
  margin: 0;
  color: #FFFFFF;
}
.footer p {
  color: #FFFFFF;
  font-family: "Roboto Slab", serif;
  font-size: 1.5rem;
}
.footer .navegacion {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .footer .navegacion {
    gap: 1rem;
  }
}
.footer .navegacion div {
  margin-bottom: 5rem;
  display: flex;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .footer .navegacion div {
    gap: 1rem;
    flex-direction: column;
  }
}
.footer .navegacion a, .footer .navegacion h1 {
  text-decoration: none;
